import { useRef, useEffect, useContext, useMemo } from "react";
import { AppContext } from "../Home";

import styles from "./Alert.module.css";

export default function Alert({ message }) {
  const { showAlert, setShowAlert } = useContext(AppContext);
  const timeOut = useRef();
  useEffect(() => {
    if (showAlert) {
      timeOut.current = setTimeout(() => setShowAlert(false), 3000);
    }
  }, [showAlert, setShowAlert]);
  const sentence = useMemo(() => {
    switch (message) {
      case "addMusic":
        return "  .";
      case "updateMusic":
        return "  .";
      case "deleteMusic":
        return "  .";
      case "addToPlaylist":
        return "  . (  )";
      case "addToPlaylistSingle":
        return "  .";
      case "deleteFromPlaylist":
        return "  .";
      case "addPlaylist":
        return " .";
      case "updatePlaylist":
        return "  .";
      case "deletePlaylist":
        return " .";
      case "addMusicF":
        return "  ID    .";
      case "addPlaylistF":
        return "    .";
      case "updateMusicF":
        return "     .";
      case "deleteMusicF":
        return "      .";
      default:
        return ` : ${message}`;
    }
  }, [message]);
  return (
    <div
      id={styles.alert}
      className={showAlert ? styles.showing : styles.notShowing}
    >
      <span
        id={styles.closeButton}
        className="material-icons-round"
        onClick={() => {
          setShowAlert(false);
          clearTimeout(timeOut.current);
        }}
      >
        close
      </span>
      &nbsp;
      <div id={styles.sentence}>{sentence}</div>
    </div>
  );
}
